import { Component, OnInit } from '@angular/core';
import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders, HttpResponse } from '@angular/common/http';

@Component({
  selector: 'app-main-form',
  templateUrl: './main-form.component.html',
  styleUrls: ['./main-form.component.css']
})
@Injectable()
export class MainFormComponent implements OnInit {

  constructor(private http: HttpClient) { }

  weekGames = [];
  currentDay = 7;
  gameArray = [];
  maxGames = 34;
  selectedDay = this.currentDay;

  ngOnInit() {
    this.http.get<Array<any>>('https://www.openligadb.de/api/getmatchdata/bl1/2018/'+this.currentDay, { observe: 'response' }).toPromise().then(response => {
      this.weekGames = response.body
    });
    this.gameArray = Array.from(new Array(this.maxGames-this.currentDay+1),(val,index)=>index+this.currentDay);
  }

  selectDay(day) {
    this.http.get<Array<any>>('https://www.openligadb.de/api/getmatchdata/bl1/2018/'+day.charAt(0), { observe: 'response' }).toPromise().then(response => {
      this.weekGames = response.body
    });
  }

}